import React from "react";
import "./index.css";
import { Router, Route } from "react-router-dom";
import history from "./history";
import { homepage } from "./containers/homepage";

function App() {
  return (
    <Router history={history}>
      <Route exact path="/" component={homepage}/>
    </Router>
  );
}

export default App;



